.wrapper {
  width: 100%;
  max-height: 66px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.inner {
  flex: 1 1 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 58px;
  padding: 0 25px;
}

.arrow_next {
  cursor: pointer;
}

.page_line_wrapper {
  width: 100%;
  height: 8px;
  background: #d8d8d8;
}

.page_line {
  background: linear-gradient(290.47deg, #3797fa 11.33%, #45c9ff 83.66%);
  height: 8px;
  border-radius: 0px 4px 4px 0px;
  transition: width 0.2s linear;
  animation: line 3s linear;
}

.arrow_back_wrapper {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.exit {
  cursor: pointer;
  display: flex;
  align-items: center;
}

.start__inner {
  height: 66px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 25px;
}
